/*NAVIGATION*/
#nav {
  display:none;
  background-color: #ed1f24;
  font-family: 'Nanum Gothic', sans-serif;
  font-weight:bold;
  font-size: 20px;
  color: #70caf2;
  letter-spacing: 5px;
  position: relative;
 top: -3vh;
 margin: 0;
}
  

#nav li {
  display: inline;
  padding-right: 60px;
  list-style: none;
  color: #70caf2;
  
}

#nav a {
  text-decoration: none;
  color: #70caf2
}
#nav a:hover :visited {
  text-decoration: none;
  font-weight:bold;
  color: #70caf2;
}



 /*MENU BUTTON*/
 #menu {
  cursor: pointer;
  position: relative;
 left: 85vw;
 top: 55px;
 margin: 0;


  }

  .bar1, .bar2, .bar3 {
    width: 5vw;
    height: 0.8vh;
    background-color: #70caf2;
    margin: 6px 0;
    transition: 0.4s;
  }

  .bar3 {
    width: 2vw;
    height: 0.8vh;
    background-color: #70caf2;
    margin: 6px 0;
    transition: 0.4s;
  }
  
  /* Rotate first bar */
  .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 9px) ;
    transform: rotate(-45deg) translate(-9px, 9px) ;
  }
  
  /* Fade out the second bar */
  .change .bar2 {
    opacity: 0;
  }
  
  /* Rotate last bar */
  .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
    transform: rotate(45deg) translate(-8px, -8px) ;
  }


